<template>
  <div class="container">
    <div  slot="content" class="dialog-content">
      <!-- <el-button type="text" @click="dialogFormVisible = true">打开嵌套表单的 Dialog</el-button> -->
      <el-dialog title="个人信息" :visible.sync="dialogFormVisible" width="80%" :center="true">
        <div style="text-align:center;">
          <el-form :model="form" size="mini">
            <el-form-item>
              <el-select v-model="form.sex" placeholder="请选择性别">
                <el-option disabled value="">请选择</el-option>
                <el-option label="男" value="男"></el-option>
                <el-option label="女" value="女"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item>
              <el-select v-model="form.age" placeholder="请选择年龄">
                <el-option disabled value="">请选择</el-option>
                 <el-option v-for="i in 100"  v-bind:key="i" :value="i"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </div>

        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="dialogFormVisible = false">保 存</el-button>
        </div>
      </el-dialog>
    </div>

    <div class="btn" @click="toChat">
      <switch-btn></switch-btn>
    </div>
  </div>
</template>
<script>
import SwitchBtn from "../components/SwitchBtn";

export default {
  name: "index",
  mounted() {},
  data: function() {
    return {
      connectState: false,
      mu: null,
      dialogFormVisible: true,
      form: {
        sex: "男",
        age:18
      }
    };
  },
  sockets: {
    connect() {
      this.$socket.emit("matche");
    },
    repeatConnection() {
      // alert("不可以开多个浏览器哦");
      this.$socket.close();
    },
    disconnect() {
      // alert("连接断开了");
      // window.console.log("66666666666666666666666");
    },
    matche(msg) {
      msg = JSON.parse(msg);
      if (msg.success) {
        localStorage.setItem("userInfo",JSON.stringify(msg.info));
        this.$router.push("chat");

        // this.$router.push({ path: '/chat', query:msg.info});
      }
    }
  },
  components: {
    SwitchBtn
  },
  methods: {
    toChat() {
      this.$finger(x => {
        window.console.log(x);
        localStorage.setItem("age",this.form.age);
        localStorage.setItem("sex",this.form.sex);
        this.$socket.io.opts.query = {
          token: x,
          sex:localStorage.getItem("sex"),
          age:localStorage.getItem("age"),
        };
        this.$socket.connect();
      });
    }
  }
};
</script>



<style scoped lang="less">
.container {
  display: flex;
  height: 100%;
  background-color: #202832;
  .btn {
    margin: auto;
  }
}
</style>


