<template>
  <el-row type="flex" align="middle" class="qbc">
    <el-col class="contentss" :xs="5" :sm="5" :md="4" :lg="2" :xl="0">
      <el-button @click="back" type="text" icon="el-icon-back">返回</el-button>
    </el-col>

    <el-col :xs="14" :sm="14" :md="16" :lg="18" :xl="24" class="contentss">
      <el-row>
        <el-col>闲聊</el-col>
        <el-col>{{userInfo.age}}-{{userInfo.sex}}</el-col>
      </el-row>
    </el-col>

    <el-col class="contentss" :xs="5" :sm="5" :md="4" :lg="2" :xl="0">
      <el-button @click="disc" type="text">{{text}}</el-button>
    </el-col>
  </el-row>
</template>
<script>
export default {
  mounted() {
    if (this.$socket.connected) {
      this.text = "断开";
    } else {
      this.text = "匹配";
    }
  },
  data() {
    return {
      connected: this.$socket.connected,
      text: "匹配",
      loading:{},
      userInfo:JSON.parse(localStorage.getItem("userInfo"))
    };
  },
  watch: {
    connected: function(newVal) {
      if (newVal) {
        this.text = "断开";
        // 加载动画此处插入
        this.loading.close()
      } else {
        this.text = "匹配";
      }
    }
  },
  sockets: {
    //断开连接
    disc() {
      this.connected = false;
      this.$message("对方断开了连接");
    },
    //匹配方法
    matche(msg) {
      msg = JSON.parse(msg);
      if (msg.success) {
        this.userInfo=msg.info
        this.connected = true;
      }
    }
  },
  methods: {
    disc() {
      //匹配前判断当前连接是否在线
      if (!this.$socket.connected) {
        this.$finger(x => {
          this.$socket.io.opts.query = {
            token: x,
            age:localStorage.getItem("age"),
            sex:localStorage.getItem("sex")
          };
          this.$socket.connect();
        });
      }

      if (this.connected) {
        this.$socket.emit("disc");
        this.connected = false;
      } else {
        this.$socket.emit("matche");
        this.openFullScreen2()

      }
    },
     openFullScreen2() {
      this.loading = this.$loading({
      lock: true,
      text: '匹配中',
      spinner: 'el-icon-loading',
      background: 'rgba(0, 0, 0, 0.7)'
     })
    },
    back(){
      this.$socket.close()
       this.$router.push("/");
    }
  }
};
</script>
<style lang="less" scoped>
.qbc {
  height: 100%;
  background-color: aqua;
  .contentss {
    text-align: center;
  }
}
</style>




