import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import index from './views/index';
import chat from './views/chat';
const router = new VueRouter({
    routes: [
        { path: '/', component: index ,meta:{
            title:"gossip"
        }},
        { path: '/chat', component: chat },
    ]
})

export default router

