<template>
  <el-row class="con">
    <el-col class="title">
      <tiltle></tiltle>
    </el-col>

    <el-col class="content">
      <contents ref="contents"></contents>
    </el-col >

    <el-col class="biu">
      <emit @send="send"></emit>
    </el-col >
  </el-row>


  
</template>
<script>
import Tiltle from "../components/Title";
import Contents from "../components/Content";
import Emit from "../components/Emit";
export default {
  created(){

  },
  sockets: {
      connect() {
      },
      sendMsg(msg){
        msg = JSON.parse(msg);
        // var msg = {me:false,message:val}
        this.$refs.contents.refreshData(msg)
        // alert(val)
      }
    },
  methods:{
    send(x){
      var msg = {me:true,message:x.message}
      this.$socket.emit('sendMsg', JSON.stringify(x));
      this.$refs.contents.refreshData(msg)
    },
    cc(){
      alert(6666)
    }
  },
  components: {
    Tiltle,
    Contents,
    Emit
  }
};
</script>
